.layout {
  min-height: 100vh;
  background: #fafafa;
}

.content {
  padding: 80px 24px 60px;
}

.content-wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.intro-section {
  text-align: center;
  margin-bottom: 60px;
  padding: 0 24px;
}

.title-section {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin-bottom: 24px;
}

.main-title {
  font-size: 52px !important;
  font-weight: 700 !important;
  margin: 0 !important;
  color: #000 !important;
  letter-spacing: -1px;
}

.title-image {
  max-width: 500px;
  width: 100%;
  height: auto;
  display: block;
}

.expand-button {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 20px;
  color: #666;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  transition: all 0.3s ease;
  border-radius: 4px;
}

.expand-button:hover {
  background: #f5f5f5;
  color: #000;
}

.expand-button .icon-expanded {
  transform: rotate(180deg);
  transition: transform 0.3s ease;
}

.expand-button .anticon {
  transition: transform 0.3s ease;
}

.description-section {
  max-width: 800px;
  margin: 24px auto 0;
  padding: 24px;
  background: #f9f9f9;
  border-radius: 12px;
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.main-description {
  font-size: 16px !important;
  color: rgba(0, 0, 0, 0.7) !important;
  margin-bottom: 0 !important;
  line-height: 1.7 !important;
  text-align: left !important;
}

.apps-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 32px;
  padding: 0 24px;
}

.app-card {
  border-radius: 12px;
  transition: all 0.3s ease;
  height: 100%;
  border: 1px solid #e8e8e8;
  background: white;
  cursor: pointer;
}

.app-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
}

.app-icon {
  font-size: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  background: rgba(24, 144, 255, 0.1);
  border-radius: 50%;
  margin: 0 auto;
}

.footer {
  text-align: center;
  background: transparent;
  padding: 24px;
  border-top: 1px solid #f0f0f0;
}

@media (max-width: 768px) {
  .main-title {
    font-size: 36px !important;
  }

  .title-image {
    max-width: 350px;
  }

  .apps-grid {
    grid-template-columns: 1fr;
    gap: 24px;
    padding: 0;
  }

  .content {
    padding: 60px 16px 40px;
  }

  .intro-section {
    margin-bottom: 40px;
    padding: 0 16px;
  }

  .description-section {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .main-title {
    font-size: 32px !important;
  }

  .title-image {
    max-width: 280px;
  }

  .title-section {
    gap: 8px;
  }

  .expand-button {
    font-size: 18px;
    padding: 6px;
  }

  .main-description {
    font-size: 15px !important;
  }

  .content {
    padding: 40px 16px;
  }
}

